.engine-selector{width:1200px;margin:0 auto}.engine-selector .tabs{display:flex;gap:16px;margin-bottom:24px}.engine-selector .tabs .tab{flex:1;padding:14px 0;background-color:#fff;color:#000;font-weight:700;text-transform:uppercase;font-size:14px;border:1px solid #ccc;cursor:pointer;transition:background-color .2s}.engine-selector .tabs .tab.active{background-color:#06f;color:#fff;border-color:#06f}.engine-selector .table{width:100%;border-collapse:collapse}.engine-selector .table th,.engine-selector .table td{padding:12px 16px;border:1px solid #ddd}.engine-selector .table th{font-weight:700;text-align:left}.engine-selector .table th.first-col{background-color:#b3b3b3}.engine-selector .table th.second-col{background-color:#fff}.engine-selector .table td{vertical-align:top}.engine-selector .table td:nth-child(1){background-color:#b3b3b3}.engine-selector .table td:nth-child(2){background-color:#fff}.engine-selector .note{margin-top:12px;font-size:14px;font-style:italic;color:#333}@media screen and (max-width: 1200px){.engine-selector{width:100%;padding:0px 24px}}